/* TILES */
.generated-tiles{
    line-height: 1.45; 
    width: 100%;
    height: -webkit-fill-available;
    overflow-y: scroll;
    box-shadow: 0px 0px 2px #00000026;
    background: #10161A;
    align-items: baseline;
   /* padding: 8px 0;*/
    display: flex; 
    justify-content: center;
    flex-wrap: wrap; 
    justify-items: flex-start;
    align-content: baseline;
}
.tiles-row {
    padding: 8px 0;
    display: flex; 
    justify-content: center;
    flex-wrap: wrap; 
    justify-items: flex-start;
} 
.single-tile {
    padding: 0.5cm;
    cursor: pointer;
    -webkit-transition: border-radius 0.2s ease-in;
    transition: border-radius 0.2s ease-in;
}
.single-tile:hover{
    border-radius: 25%
}
.selected-tile {
    border-radius:50%
}

.steps .bp3-label.slider-label{
