.applied-color{ 
    padding: 0.4vw; 
    font-size: 9pt;
}

.applied-colors-container{ 
    display: flex; 
    justify-content: start; 
    flex-wrap: wrap; 
    overflow: auto;
    align-content: baseline;
    max-width: 300px;
}
.try-colors-container{ 
    display: flex;
    justify-content: space-between

}
.try-card-holder{
    max-width: 836px;
    font-size: 1em;
}
#try-background{
    cursor: pointer; 
}
.try-title-holder{  
    font-family: 'Lora', serif; 
    border-bottom: 1px solid #e8e8e8; 
    padding-bottom: 12px 
}
.reset-try-card-btn{
    float: right
}

.applied-colors-header{
    
    margin-top: 16px;
    display: flex; 
    justify-content: space-between; 
    align-items: center; 
}